.About h1 {
  margin-top: 10%;
}

.About p {
  padding-left: 5%;
  padding-right: 5%;
}

.AboutText {
  text-align: justify;
}

.AboutCardGroup {
  padding-right: 10%;
  padding-left: 10%;
  padding-top: 5%;
}

.ProfilePic {
  position: relative;
  width: 100%;
}

@media (min-width: 768px) {
  .ProfilePic {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
  }
}
