.EndBanner p {
  color: black;
  position: relative;
  top: 50%;
  transform: translateY(50%);
}

.EndBanner {
  background-color: white;
  min-height: auto;
}

.EndBanner h1 {
  margin-top: 10%;
}

.ContactBtn {
  background-color: white;
  border: 0;
  margin-right: 10%;
  margin-left: 10%;
  display: inline-block;
}

.ContactBtnContainer {
  padding-left: 20%;
  padding-right: 20%;
  padding-bottom: 100px;
  padding-top: 5%;
}

/* 2D TRANSITIONS */
/* Grow */

.ContactBtn:hover {
  -webkit-transform: scale(1.4);
  transform: scale(1.4);
}
